/* wf-header - Start */
.wf-header__banner {
  display: block;

  width: 100%;
}

.wf-header__download {
  margin-right: 11px;
  margin-left: auto;

  @media screen and (max-width: 500px) {
    margin-left: auto;

    &-text {
      display: none;
    }
  }
}

.wf-header__row {
  display: flex;
  align-items: center;
  justify-content: space-between;
  height: 80px;
  padding: 10px 30px;
  background: #9e79b6;

  @media screen and (max-width: 500px) {
    padding: 10px 20px;
    height: unset;
  }
}

.wf-header__nav {
  position: relative;
  width: 100%;
  max-width: 395px;

}

.wf-header__nav.active .wf-header__nav-list {
  display: block;
}

.wf-header__nav.active .wf-header__nav-toggle-icon i:nth-child(1) {
  transform: translateY(14px) rotate(45deg);
}

.wf-header__nav.active .wf-header__nav-toggle-icon i:nth-child(2) {
  transform: scaleX(0);
}

.wf-header__nav.active .wf-header__nav-toggle-icon i:nth-child(3) {
  transform: translateY(-14px) rotate(-45deg);
}

.wf-header__nav-toggle {
  position: relative;
  z-index: 110;
  display: flex;
  align-items: center;
  padding: 7px 20px;
  text-transform: uppercase;
  color: #fff;
  font-size: 24px;
  font-weight: bold;
  line-height: 29px;
  background-color: transparent;
  border: none;

  @media screen and (max-width: 500px) {
    padding: 0;
  }
}

.wf-header__nav-toggle-icon {
  display: block;

  margin-right: 16px;
}

.wf-header__nav-toggle-icon i {
  display: block;

  width: 45px;
  height: 5px;
  margin: 9px 0;

  transition: transform .2s ease-in-out;

  border-radius: 100px;
  background: #fff;
}

.wf-header__nav-list {
  position: absolute;
  z-index: 100;
  top: 65px;
  right: 0;
  left: 0;

  display: none;

  padding: 15px 0;

  animation: fadeIn .2s ease-in-out;
}

.wf-header__nav-list:before {
  position: absolute;
  z-index: -1;
  top: -65px;
  right: 0;
  bottom: 0;
  left: 0;

  display: block;

  content: '';

  background: #9e79b6;
  box-shadow: 0px 4px 4px rgba(0, 0, 0, 0.25);
}

.wf-header__nav-item {
  display: block;

  padding: 15px 20px;

  text-decoration: none;
  text-transform: uppercase;

  color: #fff;

  font-size: 14px;
  font-weight: bold;
  line-height: 17px;
}

/* wf-header - End */

